.tours__buttons-container {
  position: absolute;
  top: 0;
  right: 0;

  @include vp-1199 {
    top: -5px;
  }

  @include vp-767{
    top: 2px;
  }
}

.tours__back-button {
  background-color: $color-secondary;
  border: none;
  padding: 11px 12px;
}

.tours__next-button {
  background-color: $color-secondary;
  border: none;
  transform: rotate(180deg);
  padding: 11px 12px;
}

.tours__button-icon {
  display: flex;
  align-self: center;
}
